import Vue from 'vue';
import Router from 'vue-router';

//  routes
import Home from './views/Home.vue';
import Links from './views/Links.vue';
import Projects from './views/projects.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/links',
      name: 'links',
      component: Links,
    },
      {
          path: '/projects',
          name: 'Projects',
          component: Projects,
      }
  ],
});
